.center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.title {
  text-align: center;
  margin: 20px 0 20px 0;
}

.wd-100 {
  width: 100%;
}

.bg-purple {
  background-color: rgb(129, 88, 197);
}

.bg-orange {
  background-color: rgb(255, 162, 0);
}

.relative {
  position: relative;
}


/* Img */

.responsive {
  width: 100%;
  max-height: 100%;
  display: block;
  border-radius: 4px;
}

/* Card */
.card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  padding: 15px;
}

.card-img {
  width: 80%;
  margin-left: 10%;
}

.card-text {
  font-size: 1.2rem;
}

.card-footer {
  font-size: 1rem;
}

/* Categoria.Banner_Produtos */
#categoria {
  margin-top: 50px;
}

.card-categoria {
  display: flex;
  align-items: center;
  justify-content: space-between;

  height: 100%;
  max-height: 160px;
}

.card-cat {
  text-align: center;
  padding: 10px;
  position: relative;
}

.card-cat:hover img {
  opacity: 0.7;
  color: rgb(255, 162, 0);
}

.card-cat:hover p {
  color: rgb(255, 162, 0);
  background-color: #fff;
}

.img-cat {
  opacity: 1;
  width: 100%;
  height: 9rem;
  max-height: 140px;
  border-radius: 5px;
}

.txt-cat {
  position: absolute;
  color: #fff;
  background-color:rgb(255, 162, 0);
  border-radius: 5px;
  font-weight: bold;
  font-size: 1.5rem;
  padding: 2px;
  cursor: pointer;
}

.txt-cat-center {
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.txt-cat-left {
  bottom: 0%;
  right: 0%;
  transform: translateX(-8%) translateY(-50%);
}

.txt-cat-right {
  bottom: 0%;
  left: 0%;
  transform: translateX(15%) translateY(-50%);
}







/* Header */

.search {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.fa-search {
  border: 1px solid;
  border-left: none;
  padding: 5%;
  font-size: 1rem;
  cursor: pointer;
}

.search-input {
    width: 100%;
    border: 1px solid;
    border-right: none;
    padding: 5%;
    font-size: 0.8rem;
}

.search:hover > .search-input {
  color: rgb(255, 162, 0);
  border-color: rgb(255, 162, 0);
}

.search:hover > .fa-search {
  color: rgb(255, 162, 0);
  border-color: rgb(255, 162, 0);
}

#logon {
