}

.fa-shopping-cart {
    font-size: 25px;
    color: rgb(127,127,127);
    padding: 4px;
    cursor: pointer;
}

.fa-shopping-cart:hover {
  color: rgb(255, 162, 0);
}

/* Sociais */

.btn-close {
  border: none;
  position: fixed;
  bottom: 25%;
  right: 15%;
  background-color: rgb(129, 88, 197);
  border-radius: 5px;
  cursor: pointer;
  padding: 1px;
}

.btn-close-bar {
  color: #fff !important;
}

.fa-arrow-up {
  font-size: 40px;
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.8);
  padding: 8px;
  background-color: rgba(127,127,127, 0.8);
  position: fixed;
  bottom: 15%;
  right: 15%;
}

.fa-whatsapp {
  font-size: 40px;
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.8);
  padding: 8px;
  background-color: rgba(77, 194, 71, 0.8);
  position: fixed;
  bottom: 5%;
  right: 15%;
}
  
#btn-fixed {
  z-index: 99;
  border: none;
}

#btn-fixed-close {
  display: none;
}

/* Menu */

#menu {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 90;
    opacity: 0.8;
  }
  
  .menu, li {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: rgb(129, 88, 197);
    color: #fff;
    text-decoration: none;
    list-style: none;
    text-align: center;
    min-height: 30px;
    cursor: pointer;
  
    border-radius: 5px;
    position: relative;
    z-index: 1;
  }
  
  .menu li:hover {
    background-color: rgb(255, 162, 0);
    color:rgb(129, 88, 197);
  }
  
  /* Submenus */
  
  .menu ul {
    display: none;
    position: absolute;
    background-color: rgb(129, 88, 197);
    top: 25px;
    width: 100%;
    border-radius: 5px;
  }
  
  .menu ul:hover {
    background-color: rgb(255, 162, 0);
    color:rgb(129, 88, 197);
  }
  
  .menu ul ul {
    top: 0px;
    left: 100%;
  }
  
  .menu li:hover > ul {
    display: block;
  }
  
  li:hover {
    color: rgb(129, 88, 197);
  }
  
  /* Menu Responsivo */
    
  #btn_menu {
    display: none;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
    border: none;
    background-color: rgb(129, 88, 197);
    color: rgb(255, 162, 0);
  }
  
  @media (max-width: 830px) {
    #menu_hide {
      display: none;
    }
    
    #btn_menu {
      display: block;
    }
  }
  
  
  @media(max-width: 600px){
  .menu {
    flex-direction: column;
    padding-bottom: 15px;
  }

  .menu, li {
    align-items: flex-start;
    justify-content: flex-start;
  }

  .menu ul {
    top: 0;
    left: 100%;
  }
  
  .btn-bar {
    background-color: rgb(129, 88, 197);
    border-radius: 5px;
    padding: 3px;
    color: rgb(255, 162, 0);
  }

}
